    const fetchOpenWeatherApi = async (lat = 35.8683476, lon = 128.5940482) => {
      // API Ìò∏Ï∂úÏùÑ ÏúÑÌïú ÌïÑÏàò Îç∞Ïù¥ÌÑ∞
