      const API_KEY = "11d451fb1dc708c9efd7b05b16f080d4";
      let initialLat = lat;
      let initialLon = lon;
      try {
        const res = await axios.get(
          `https://api.openweathermap.org/data/2.5/weather?lat=${initialLat}&lon=${initialLon}&appid=${API_KEY}&units=metric`
        );
