        temporaryData[1].value = isInitialWindSpeed;
        temporaryData[2].value = Math.round(isInitialFeel) + "ÎèÑ";

        // ÌôîÎ©¥ Ï∂úÎ†• Îç∞Ïù¥ÌÑ∞ ÎπÑÍµê
        showData();

