          <ul class="depth1">
            <li>Îß•ÏΩîÌä∏</li>
            <li>Í∏¥ÌåîÌã∞</li>
            <li>Í∏¥Î∞îÏßÄ</li>
          </ul>
          `;
      } else if (earlySummer) {
        outPutHtml.value = ` 
          <ul class="depth1">
            <li>Î∞òÌåîÌã∞</li>
            <li>Î©¥Î∞îÏßÄ</li>
            <li>Ïä§ÎãàÏª§Ï¶à</li>
